import { stringifyDate } from './date';
export const assignProperties = (content, comments, flags) => {
  const commentArr = comments.map(comment => {
    comment.createdon = stringifyDate(comment.createdon);
    return comment
  });
  const feed = content.map(el => {
    const filterComments = commentArr.filter(comment => comment.contentid === parseInt(el.id, 10));
    console.log(filterComments);
    const flagStat = flags.filter(flag => flag.contentid === el.id);
    const { createdon } = el;
    el.comments = filterComments;
    el.createdon = stringifyDate(createdon);
    el.flagStat = flagStat
    return el
  })
  return feed
}

export const assignPropsToOneFeed = (content, comments, flagStats) => {
  const commentArr = comments.map(comment => {
    comment.createdon = stringifyDate(comment.createdon);
    return comment;
  })
  content.comments = commentArr;
  content.flagStats = flagStats;
  content.createdon = stringifyDate(content.createdon)
  return content;
}