import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { getData, modifyData, apiKey } from '../../../GeneralComponents/AJAX/HttpRequest';
class Comment extends Component {
  constructor(props) {
      super(props);
      this.state = { 
          flagStats: []
        }
  }
  componentDidMount() {
     this.fetchFlagStats()
  }

  fetchFlagStats =() => {
    const { feedId, feedType, userData, comment: { id } } = this.props;
    const { userId, token } = userData;
    const contentType = feedType === 'article' ? 'articles' : 'gifs';
    getData(`${apiKey}/${contentType}/${feedId}/comment/${parseInt(id, 10)}/flagStats`, userId, token )
    .then(res => {
        const { data: { flagStats } } = res;
        this.setState({flagStats: flagStats});
        console.log(this.state);
        console.log(flagStats)
    }).catch(err => {
        console.log(err)
    })
  }

  flag = (event) => {
    event.preventDefault();
    const { feedId, feedType, userData: { userId, token }, comment: { id } } = this.props;
    const { target } = event;
    const bool = target.classList.contains('danger-text') ? false : true;
    const data = {
      isFlagged: bool,
      userId
    }
    modifyData(`${apiKey}/${feedType}s/${feedId}/comment/${parseInt(id, 10)}/flag`, data, token )
      .then((res) => {
        this.fetchFlagStats()
      }).catch(err => {
        console.log(err)
      })
  }

  checkIfUserFlagged = (flagStats, userId) => {
    return flagStats.find(arr => parseInt(arr.userid, 10) === userId);
  }
  render() {
    const { comment, users, userData: { userId } } = this.props;
    const { flagStats } = this.state;
    const author = users.find(user => user.employeeId === comment.userid);
    const { photoDetails, firstName, lastName, employeeId: authorId } = author
    return ( 
      <div className="margin-bottom-sm border-line--h">
        <div className="margin-bottom-sm">
          <div className="d-flex justify-content--s-between align-items--center margin-bottom-sm">
            <div className="poster--details">
              <Link to={authorId === parseInt(userId, 10) ? '/user/profile' : `/user/${firstName}_${lastName}`}
                style={{color: '#000'}}>
                <div className="d-flex nowrap">
                  <img className="avatar--sm icon" 
                  src={photoDetails[0]}
                    alt="poster avatar" />
                  <div className="d-flex column">
                    <span className="poster--username text-content font-weight-bold margin-bottom-xsm">{`${firstName} ${lastName}`}</span>
                    <div className="margin-bottom-sm">
                      <span className="d-block font-sm gray-color">{comment.createdon}</span>
                    </div>
                  </div>
                </div>
              </Link>
            </div>
            <div className="flag-stats ">
              <i className={`fas fa-flag margin-right-sm cursor-pointer ${this.checkIfUserFlagged(flagStats, userId) ? 'danger-text' : ''}`}
                onClick={(event) => this.flag(event)}></i>
              <span className="flag font-weight-bold font-sm padding-sm border-r-circle">{flagStats.length === 0 ? '' : flagStats.length}</span>
            </div>
          </div>
        </div>
        <div className="comment-content margin-bottom-sm">
          <div className="text-content padding-bottom-sm">
            <p>{comment.comment}</p>
          </div>
        </div>
      </div>
    );
  }
}

export default Comment;