import React, { Component } from 'react';
import { HashRouter as Router, Switch, Route } from 'react-router-dom';
import FeedContextProvider, { FeedConsumer } from '../../GeneralComponents/context/feedContext';
import { UserContext } from '../../GeneralComponents/context/userContext';
import ProtectedRoute from '../../GeneralComponents/PrivateRoute/ProtectedRoute.component';
import { IsAuth } from '../../GeneralComponents/context/auth.component';
import './mainContent.style.css';
//components
import NewsFeed from './NewsFeed/NewsFeed.component';
import SingleContent from './NewsFeed/singleContent.component';

class MainContent extends Component {
  constructor(props) {
      super(props);
      this.state = {  }
  }

  static contextType = UserContext;
  render() {
    const { data: { userId, token } } = this.context;
    const { match: { path } } = this.props;
    console.log(path);
    return (
      <main className="main">
        <div className="content-wrapper">
          <Router>
            <FeedContextProvider>
              <FeedConsumer>
                {({updateFeed, feed}) => (
                  <Switch>
                    <Route exact path={`${this.props.match.path}`} render={(props) => <NewsFeed updateFeed={updateFeed} feed={feed} props={{...props}} />} />
                    <Route path={`${path}/articles/:articleId`}
                      render={(props) => <SingleContent userData={{ userId, token }} updateFeed={updateFeed} allFeed={feed} props={{...props}} />}
                    />
                    <Route path={`${path}/gifs/:gifId`}
                      render={(props) => <SingleContent userData={{ userId, token }} updateFeed={updateFeed} allFeed={feed} props={{...props}} />}
                    />
                  </Switch>
                )}
              </FeedConsumer>
            </FeedContextProvider>
          </Router>
        </div>
      </main>
    );
  }
}

export default MainContent;